<template>
  <div class="recommend">
    <div class="title">
      周末去哪儿
    </div>
    <ul class="recommend-list">
      <li
      v-for="item of list"
      :key = "item.id"
      class="recommend-item">
        <div class="recomment-pic-con">
          <img class="recomment-pic" :src="item.imgUrl" :alt="item.title">
        </div>
        <ul class="recommend-desc">
          <li class="recommend-desc-title ellipsis">{{item.title}}</li>
          <li class="recommend-desc-detail ellipsis">{{item.desc}}</li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecomment',
  props: {
    list: Array
  }
}
</script>

<style lang="scss" scoped>
  .recommend {
    background: #fff;
    .title {
      font-size: 32px;
      padding: 20px 0 20px 30px;
      background: #f7f3f3;
    }
    .recommend-list{
      .recommend-item{
        .recomment-pic-con{
          width: 100%;
          img{
            width:100%;
            height: 100%;
          }
        }
        .recommend-desc {
          margin:20px;
          .recommend-desc-title{
            font-size:32px;
            color:#212121;
          }
          .recommend-desc-detail{
            margin-top: 20px;
            font-size:30px;
            color: #847d7d;
          }
        }
      }
    }
  }
</style>



